<template>
  <div class="main-container">
    <!--      -->
    <div class="search-container">
      <input
        v-model="searchQuery"
        class="search-bar"
        type="text"
        placeholder=" "
        @click="searchHotel"
      />
      <!--   -->
      <button @click="searchHotel" class="search-button"></button>
    </div>

    <!--    -->
    <ul v-if="searchQuery.length > 0" class="autocomplete-list">
      <li
        v-for="result in autocompleteResults"
        :key="result.hotelId"
        @click="goToHotelDetail(result.hotelId)"
        class="autocomplete-item"
      >
        <span class="autocomplete-hotel-name">{{ result.name }}</span>
        <!--    -->
        <span class="hotel-address">{{
          result.address || "  "
        }}</span>
        <!--   -->
      </li>
      <li v-if="noResults" class="no-results">  .</li>
    </ul>

    <!--    -->
    <button @click="fetchRandomHotels" class="refresh-button"></button>
    <div class="hotel_list_container">
      <div class="hotel_grid">
        <div
          v-for="hotel in randomHotels"
          :key="hotel.hotelId"
          class="hotel-container"
        >
          <router-link :to="`/hotel-details/${hotel.hotelId}`">
            <img
              :src="hotel.imageUrl || defaultImage"
              class="img-container"
              alt="Hotel Image"
            />
          </router-link>
          <div class="hotel-name">{{ hotel.name }}</div>
          <div class="hotel-info">
            <span class="rating">{{ hotel.rating || 0 }}</span>
            <span>({{ hotel.reviewCount }})</span>
          </div>
        </div>
      </div>

      <h2 class="review-title"> Top 10</h2>
      <div class="hotel_grid">
        <div
          v-for="hotel in topByReviewCount"
          :key="hotel.hotelId"
          class="hotel-container"
        >
          <router-link :to="`/hotel-details/${hotel.hotelId}`">
            <img
              :src="hotel.imageUrl || defaultImage"
              class="img-container"
              alt="Hotel Image"
            />
          </router-link>
          <div class="hotel-name">{{ hotel.name }}</div>
          <div class="hotel-info">
            <span class="rating">{{ hotel.rating || 0 }}</span>
            <span>({{ hotel.reviewCount }})</span>
          </div>
        </div>
      </div>

      <h2 class="review-title"> Top 10</h2>
      <div class="hotel_grid">
        <div
          v-for="hotel in topByRating"
          :key="hotel.hotelId"
          class="hotel-container"
        >
          <router-link :to="`/hotel-details/${hotel.hotelId}`">
            <img
              :src="hotel.imageUrl || defaultImage"
              class="img-container"
              alt="Hotel Image"
            />
          </router-link>
          <div class="hotel-name">{{ hotel.name }}</div>
          <div class="hotel-info">
            <span class="rating">{{ hotel.rating || 0 }}</span>
            <span>({{ hotel.reviewCount }})</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { getHotelsReviewsTop } from "@/api/hotel";

export default {
  name: "MainPage",
  data() {
    return {
      searchQuery: "",
      autocompleteResults: [],
      randomHotels: [],
      topByReviewCount: [],
      topByRating: [],
      defaultImage:
        "https://png.pngtree.com/png-vector/20240613/ourlarge/pngtree-modern-hotel-icon-with-palm-trees-black-isolated-on-white-background-vector-png-image_7010310.png",
      noResults: false, //       
    };
  },
  created() {
    this.fetchRandomHotels();
    this.fetchTopHotelList();
  },
  methods: {
    async fetchRandomHotels() {
      try {
        const response = await axios.get(
          "http://localhost:8081/api/hotels/random"
        );
        this.randomHotels = response.data; //     
      } catch (error) {
        console.error("      :", error);
      }
    },
    fetchAutocompleteResults() {
      this.autocompleteResults = []; //     

      if (this.searchQuery.length > 0) {
        const queryWithoutSpaces = this.searchQuery.replace(/\s+/g, ""); //  
        axios
          .get(
            `http://localhost:8081/api/hotels/search?query=${queryWithoutSpaces}`
          )
          .then((response) => {
            this.autocompleteResults = response.data;
            this.noResults = this.autocompleteResults.length === 0;
          })
          .catch((error) => {
            console.error("      :", error);
            this.autocompleteResults = [];
            this.noResults = true;
          });
      } else {
        this.autocompleteResults = []; //     
        this.noResults = false;
      }
    },
    searchHotel() {
      //      ,    
      this.$router.push({
        path: "/search-results",
        query: { query: this.searchQuery },
      });
    },
    goToSearchPage() {
      this.$router.push({
        path: "/search-page",
        query: { query: this.searchQuery },
      });
    },
    async fetchTopHotelList() {
      try {
        //  
        const response = await getHotelsReviewsTop();

        //    
        let topByReviewCount = response.data.topByReviewCount;
        let topByRating = response.data.topByRating;

        //       (     )
        topByReviewCount = topByReviewCount.sort((a, b) => {
          if (b.reviewCount !== a.reviewCount) {
            return b.reviewCount - a.reviewCount; //   :    
          }
          return a.name.localeCompare(b.name); //     
        });

        //      (    )
        topByRating = topByRating.sort((a, b) => {
          if (b.rating !== a.rating) {
            return b.rating - a.rating; //  :    
          }
          return a.name.localeCompare(b.name); //    
        });

        //   Vue   
        this.topByReviewCount = topByReviewCount;
        this.topByRating = topByRating;
      } catch (error) {
        console.error("    : ", error);
      }
    },
  },
};
</script>

<style scoped>
.main-container {
  width: 60%;
  margin: auto;
}

.search-container {
  position: relative; /*       */
  display: flex;
  align-items: center;
  width: 100%; /*       */
  background-color: rgb(233, 233, 233);
  border-radius: 15px;
}

.search-bar {
  width: 100%;
  height: 40px;
  font-size: 15px;
  border: none;
  outline: none;
  padding-right: 40px; /*     */
  padding-left: 10px;
  background-color: transparent;
  margin-right: 10px;
}

.search-button {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  font-size: 20px;
}

.refresh-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 5px;
  font-size: 24px;
  vertical-align: middle;
}

.autocomplete-list {
  list-style: none;
  padding: 0;
  margin-top: 5px;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 8px;
  max-height: 150px;
  overflow-y: auto;
  position: absolute;
  width: 100%;
}

.autocomplete-item {
  display: flex;
  justify-content: space-between;
  padding: 8px;
  cursor: pointer;
  border-bottom: 1px solid #eee;
}

.autocomplete-hotel-name {
  font-size: 16px;
  color: #333;
  font-weight: bold;
}

.hotel-address {
  font-size: 14px;
  color: #777;
}

.autocomplete-item:hover {
  background-color: #f0f0f0;
}

.no-results {
  padding: 8px;
  color: gray;
  text-align: center;
}

.hotel-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hotel_grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 16px;
  margin-bottom: 30px;
}

.img-container {
  width: 180px;
  height: 120px;
  object-fit: cover;
  border-radius: 10px;
}

.hotel-name {
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  width: 90%;
  max-width: 140px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 5px;
}

.hotel-info {
  text-align: center;
}

.review-title {
  margin-top: 20px;
}

@media (max-width: 1600px) {
  .main-container {
    width: 80%;
  }
  .hotel_grid {
    grid-template-columns: repeat(4, 1fr); /* 4  */
  }
  .img-container,
  .search-bar {
    width: 200px; /*     */
  }
  .img-container {
    height: 150px;
  }
  .search-bar {
    height: 35px;
  }
}

/* 1200px   */
@media (max-width: 1200px) {
  .main-container {
    width: 80%;
  }
  .hotel_grid {
    grid-template-columns: repeat(3, 1fr); /* 3  */
  }
  .img-container,
  .search-bar {
    width: 200px; /*     */
  }
  .img-container {
    height: 150px;
  }
  .search-bar {
    height: 35px;
  }
}

/* 800px   */
@media (max-width: 800px) {
  .main-container {
    width: 85%;
  }
  .hotel_grid {
    grid-template-columns: repeat(2, 1fr); /* 2  */
  }
  .img-container,
  .search-bar {
    width: 200px; /*     */
  }
  .img-container {
    height: 150px;
  }
  .search-bar {
    height: 30px;
  }
}

/* 558px   */
@media (max-width: 558px) {
  .main-container {
    width: 90%;
  }
  .hotel_grid {
    grid-template-columns: repeat(1, 1fr); /* 1  */
  }
  .img-container,
  .search-bar {
    width: 200px; /*     */
  }
  .img-container {
    height: 150px;
  }
  .search-bar {
    height: 28px;
  }
}

/*    ( ) */
@media (max-width: 375px) {
  .main-container {
    width: 95%;
  }
  .hotel_grid {
    grid-template-columns: 1fr; /* 1  */
  }
  .img-container,
  .search-bar {
    width: 100%; /*     */
  }
  .img-container {
    height: 80px;
  }
  .search-bar {
    height: 28px;
  }
}
</style>
