<template>
  <div class="modal-overlay" @click.self="closeModal">
    <div class="modal-content">
      <h2 v-if="isEdit"> </h2>
      <h2 v-else> </h2>
      <textarea
        v-model="reviewContent"
        placeholder="  "
        rows="4"
      ></textarea>
      <div class="rating-container">
        <span
          v-for="star in 5"
          :key="star"
          class="star"
          :class="{ selected: star <= rating }"
          @click="setRating(star)"
        >
          
        </span>
      </div>
      <div class="image-upload-container" v-if="!isEdit">
        <input
          type="file"
          accept="image/*"
          multiple
          @change="handleImageUpload"
        />
      </div>
      <div class="image-preview">
        <div
          v-for="(image, index) in uploadedImages"
          :key="index"
          class="image-preview-item"
        >
          <img :src="image.url" alt="Preview" />
          <button v-if="!isEdit" @click="removeImage(index)"></button>
        </div>
      </div>
      <div class="modal-actions">
        <button @click="submitReview">{{ isEdit ? "" : "" }}</button>
        <button @click="closeModal"></button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    isEdit: Boolean,
    initialData: Object,
  },
  data() {
    return {
      reviewContent: "", //  
      rating: 0, // 
      uploadedImages: [], // { file: File, url: string }  
    };
  },
  methods: {
    setRating(star) {
      this.rating = star;
    },
    handleImageUpload(event) {
      if (this.isEdit) return; //    
      const files = Array.from(event.target.files);

      if (!files.length) {
        console.error("   .");
        return;
      }

      files.forEach((file) => {
        const reader = new FileReader();
        reader.onload = (e) => {
          if (e.target && e.target.result) {
            this.uploadedImages.push({ file, url: e.target.result }); //  URL  
          } else {
            console.error("  :", e);
          }
        };
        reader.readAsDataURL(file); //  Base64 URL 
      });
    },
    removeImage(index) {
      this.uploadedImages.splice(index, 1); //   
    },
    submitReview() {
      if (!this.reviewContent || this.rating <= 0) {
        alert("    .");
        return;
      }

      if (!this.uploadedImages || !Array.isArray(this.uploadedImages)) {
        alert("   .");
        console.error("uploadedImages is not an array:", this.uploadedImages);
        return;
      }

      // FormData    
      const formData = new FormData();
      formData.append("content", this.reviewContent);
      formData.append("rating", this.rating);

      this.uploadedImages.forEach((image, index) => {
        if (image.file) {
          formData.append("images", image.file);
        } else {
          console.warn(`    (index: ${index}):`, image);
        }
      });

      console.log("  :", {
        content: this.reviewContent,
        rating: this.rating,
        images: this.uploadedImages,
      });

      this.$emit("submit", {
        content: this.reviewContent,
        rating: this.rating,
        images: this.uploadedImages,
      });
    },
  },
  watch: {
    initialData: {
      immediate: true,
      handler(newData) {
        if (newData) {
          this.reviewContent = newData.content || "";
          this.rating = newData.rating || 0;
          this.uploadedImages = newData.imageUrl
            ? newData.imageUrl.map((url) => ({ file: null, url })) // URL   
            : [];
        }
      },
    },
  },
};
</script>

<style scoped>
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 10px;
  width: 400px;
  max-height: 80vh;
  overflow-y: auto;
}

textarea {
  width: 100%;
  height: 100px;
  resize: none;
}

.rating-container {
  display: flex;
  gap: 5px;
  margin-bottom: 10px;
}

.star {
  font-size: 24px;
  cursor: pointer;
  color: #ddd;
  transition: color 0.2s;
}

.star.selected {
  color: #ffcc00;
}

.image-upload-container {
  margin-top: 10px;
}

.image-preview {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 10px;
}

.image-preview-item {
  position: relative;
  width: 80px;
  height: 80px;
}

.image-preview-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
  border: 1px solid #ddd;
}

.image-preview-item button {
  position: absolute;
  top: 5px;
  right: 5px;
  background: red;
  color: white;
  border: none;
  border-radius: 50%;
  font-size: 12px;
  cursor: pointer;
  padding: 2px 5px;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}
</style>
