<template>
  <div>  ...</div>
</template>

<script>
import { useAuthStore } from "@/store/register_login"; // Pinia  
import { useRouter, useRoute } from "vue-router"; // Vue Router 
import axios from 'axios'; // axios 

export default {
  name: "OAuthRedirectHandler",
  setup() {
    const authStore = useAuthStore();
    const router = useRouter();
    const route = useRoute();

    //    
    const handleToken = async () => {
      //     
      const token = route.query.token;

      if (token) {
        //  Pinia     
        authStore.setAccessToken(token);

        try {
          //     
          const response = await axios.get("http://localhost:8081/api/users/me", {
            headers: {
              Authorization: `Bearer ${token}`
            }
          });

          if (response && response.status === 200) {
            //   
            const userInfo = response.data;
            authStore.currentUser = userInfo.email;
            authStore.userId = userInfo.userId;
            authStore.userName = userInfo.name;
            authStore.phone = userInfo.phone;
            authStore.LoggedIn = true;

            //        (  )
            localStorage.setItem('userInfo', JSON.stringify({
              userName: userInfo.name,
              userId: userInfo.userId,
              email: userInfo.email,
              phone: userInfo.phone,
            }));
            sessionStorage.setItem('token', token);

            //   
            router.push({ path: "/" });
          } else {
            console.error("    .");
            alert("    .   .");
            router.push({ path: "/login" });
          }
        } catch (error) {
          console.error("   :", error);
          alert("   .   .");
          router.push({ path: "/login" });
        }
      } else {
        alert(" .  .");
        //      
        router.push({ path: "/login" });
      }
    };

    //    handleToken 
    handleToken();

    return {};
  },
};
</script>
