import axios from 'axios';

/** axios  url    */
const config = axios.create({
  baseURL: 'http://localhost:8081/api',  // Spring Boot API 
  headers: {
    'Content-Type': 'application/json'
  }
});

function getHotelsReviewsTop(){
    return config.get('/hotels/top10');
}


export {
    getHotelsReviewsTop
}