<template>
  <div class="content">
    <div class="user-info">
      <h2> </h2>
      <p>: {{ userName || "  ." }}</p>
      <p>: {{ email || "  ." }}</p>
      <p>: {{ phone || "  ." }}</p>
    </div>
  </div>
  <!--   -->
  <div class="review-section">
    <h2></h2>
    <div class="review-list">
      <div v-if="reviews.length === 0" class="no-reviews">
          .
      </div>
      <div class="review" v-for="(review, index) in reviews" :key="index">
        <h3 class="hotel-name" @click="goToHotel(review.hotelId)">
          {{ review.hotelName }}
        </h3>
        <div class="review-header">
          <!--   -->
          <div class="review-rating">
            <span
              v-for="star in 5"
              :key="star"
              class="star"
              :class="{ filled: star <= review.rating }"
            >
              
            </span>
          </div>
          <span class="review-date">{{ review.date }}</span>
        </div>
        <div class="review-content">{{ review.content }}</div>

        <!--   -->
        <div class="image-gallery">
          <img
            v-for="(image, imgIndex) in review.imageUrl"
            :src="image"
            :key="imgIndex"
            class="thumbnail"
            @click="openLightbox(image)"
          />
        </div>

        <!-- /  -->
        <div class="review-actions">
          <button class="edit-button" @click="openEditModal(review)">
            
          </button>
          <button class="delete-button" @click="confirmDelete(review.reviewId)">
            
          </button>
        </div>
      </div>
    </div>

    <!-- Lightbox -->
    <div v-if="lightboxImage" class="lightbox" @click="closeLightbox">
      <img :src="lightboxImage" alt="Review Image" />
    </div>

    <!--  /  -->
    <ReviewModal
      v-if="isModalOpen"
      :isEdit="isEditMode"
      :initialData="selectedReviewData"
      @submit="handleReviewSubmit"
      @close="closeModal"
    />
  </div>
</template>

<script>
import ReviewModal from "@/components/UserPages/reviewModal.vue";
import { updateReview, deleteReview } from "@/api/api";

export default {
  props: {
    userName: {
      type: String,
      required: true,
    },
    email: {
      type: String,
      required: true,
    },
    phone: {
      type: String,
      required: true,
    },
    reviews: Array,
    loggedInUserId: Number,
  },
  components: {
    ReviewModal,
  },
  data() {
    return {
      isModalOpen: false, //   
      isEditMode: false, // /  
      selectedReviewData: null, //   
      lightboxImage: null, //  
    };
  },
  methods: {
    openEditModal(review) {
      this.isModalOpen = true; //  
      this.isEditMode = true; //   
      this.selectedReviewData = { ...review }; //    
    },
    closeModal() {
      this.isModalOpen = false; //  
      this.isEditMode = false; //   
      this.selectedReviewData = null; //    
    },
    async handleReviewSubmit(reviewData) {
      try {
        const token = sessionStorage.getItem("token");
        if (this.isEditMode && this.selectedReviewData) {
          await updateReview(
            this.selectedReviewData.reviewId,
            reviewData,
            token
          );
          alert(" .");
        } else {
          alert("  .");
        }
        this.closeModal();
        this.$emit("update"); //     
      } catch (error) {
        console.error("    :", error);
        alert("    .");
      }
    },
    async confirmDelete(reviewId) {
      if (confirm("   ?")) {
        try {
          const token = sessionStorage.getItem("token");
          await deleteReview(reviewId, token);
          alert(" .");
          this.$emit("update"); //     
        } catch (error) {
          console.error("    :", error);
          alert("    .");
        }
      }
    },
    openLightbox(image) {
      this.lightboxImage = image;
    },
    closeLightbox() {
      this.lightboxImage = null;
    },
    goToHotel(hotelId){
      this.$router.push(`/hotel-details/${hotelId}`);
    }
  },
};
</script>

<style scoped>
/*     */
.user-info {
  background: #f7f7f7;
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 20px;
}

.review-section {
  background: #ffffff;
  border-radius: 10px;
  padding: 20px;
}

.review-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.review {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
}

.review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.review-rating .star {
  font-size: 20px;
  color: #ddd;
}

.review-rating .star.filled {
  color: #ffcc00;
}

.image-gallery {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.thumbnail {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 5px;
}

.review-actions {
  display: flex;
  gap: 10px;
  margin-top: 15px;
}

button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.edit-button {
  background-color: #007bff;
  color: white;
}

.delete-button {
  background-color: #ff4d4d;
  color: white;
}

.hotel-name {
  color: #007bff;
  cursor: pointer;
  text-decoration: none; /*    */
  font-size: 18px;
  font-weight: bold;
}

.hotel-name:hover {
  text-decoration: underline; /*      */
}
</style>
