<!-- html -->
<template>
  <nav class="navbar">
    <div class="navbar_logo">
      <span class="brand_name" @click="to_home()">HotelJava</span>
    </div>
    <div class="navbar_items">
      <ul class="navbar_item" v-if="!isLoggedIn">
        <li>
          <router-link to="/login">
            <span class="login_btn"></span>
          </router-link>
        </li>
        <li>
          <router-link to="/register">
            <span class="signup_btn"></span>
          </router-link>
        </li>
      </ul>
      <ul class="navbar_item" v-else>
        <li>
          <button @click="logout" class="logout_btn"></button>
        </li>
        <li>
          <span
            class="my_page_btn"
            @click="to_my_page"
            @mouseover="hover = true"
            @mouseleave="hover = false"
          >
            {{ hover ? '' : userName+"" }}
          </span>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import { useAuthStore } from '@/store/register_login';
import { computed, ref } from 'vue';

export default {
  name: "theNavbar",
  setup() {
    const authStore = useAuthStore();

    // Pinia       computed 
    const isLoggedIn = computed(() => authStore.LoggedIn);
    const userName = computed(() => authStore.userName || ''); //   
    
    //    
    const hover = ref(false);

    //  
    const logout = () => {
      authStore.logout();
    };

    return {
      isLoggedIn,
      userName,
      hover,
      logout
    }
  },
  methods: {
    to_my_page() {
      this.$router.push("/my_page");
    },
    to_home() {
      this.$router.push("/");
    },
  },
};
</script>

<style scoped>
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #00aef0;
  border-bottom: 1px solid #e0e0e0;
  padding: 10px 20px;
}

.logo {
  height: 40px;
  margin-right: 10px;
}

.brand_name {
  font-size: 3em;
  font-weight: bold;
  color: #333;
  font-family: 'Dancing Script', cursive;
}

.navbar_item {
  align-items: center;
  display: flex; 
  list-style: none; 
  padding: 5;
  margin: 5;
}

.navbar_item button,
.navbar_item span {
  border: 0;
  background-color: transparent;
  font-size: 1.2em;
  margin-left: 20px;
}

.logout_btn {
  color: black;
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 1.2em;
}

.my_page_btn {
  color: black; 
  font-size: 1.2em; 
  margin-left: 20px;
  transition: color 0.3s;
}

.my_page_btn:hover {
  color: #555; /*         */
}

.navbar_item li {
  margin-left: 20px;
  font-size: 1.2em;
}

.navbar_item a {
  text-decoration: none;
  color: black;
  margin-left: 20px;
  font-size: 1.2em;
}

.navbar_btn {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.2em;
  margin-left: 20px;
}

@media (max-width: 768px) {
  .brand_name {
    font-size: 2em; /*    */
  }

  .navbar_item li {
    margin-left: 10px;
    font-size: 1em; /*    */
  }

  .navbar_items {
    flex-direction: column;
    align-items: flex-start;
  }

  .navbar_item {
    flex-direction: column;
    align-items: flex-start;
  }

  .navbar {
    padding: 10px;
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .brand_name {
    font-size: 1.8em; /*       */
  }

  .navbar_items {
    display: none; /*      */
  }
}
</style>
