<template>
  <div class="user-edit-container">
    <h2>  </h2>

    <form @submit.prevent="submitForm">
      <div class="form-group">
        <label for="userName"></label>
        <input
          type="text"
          id="userName"
          v-model="userName"
          placeholder=" "
        />
      </div>

      <div class="form-group">
        <label for="email"></label>
        <input
          type="email"
          id="email"
          v-model="email"
          placeholder=" "
          disabled
        />
        <p class="info-text">   .</p>
      </div>

      <div class="form-group">
        <label for="phone"></label>
        <input
          type="tel"
          id="phone"
          v-model="phone"
          placeholder=" "
        />
      </div>

      <!--    -->
      <div class="form-group">
        <label for="currentPassword">  </label>
        <input
          type="password"
          id="currentPassword"
          v-model="currentPassword"
          @input="checkPassword"
          placeholder="  "
        />
        <p v-if="!isPasswordValid && currentPassword" class="error-text">
            .
        </p>
      </div>

      <button type="submit" class="submit-button" :disabled="!isPasswordValid">
         
      </button>
    </form>

    <hr />

    <h2> </h2>
    <form @submit.prevent="sendVerificationCode">
      <div class="form-group">
        <label for="newPassword"> </label>
        <input
          type="password"
          id="newPassword"
          v-model="newPassword"
          placeholder="  "
        />
      </div>

      <div class="form-group">
        <label for="passwordConfirmation"> </label>
        <input
          type="password"
          id="passwordConfirmation"
          v-model="passwordConfirmation"
          placeholder="  "
        />
      </div>

      <button
        type="submit"
        class="send-email-button"
        :disabled="newPassword !== passwordConfirmation"
      >
          
      </button>
    </form>

    <!--    ( ) -->
    <!--
    <div v-if="isEmailSent" class="form-group">
      <label for="verificationCode"> </label>
      <input
        type="text"
        id="verificationCode"
        v-model="verificationCode"
        placeholder="   "
      />
      <button
        type="button"
        class="submit-button"
        @click="changePassword"
        :disabled="verificationCode.length === 0"
      >
         
      </button>
    </div>
    -->

    <!--    (  ) -->
    <div v-if="isEmailSent" class="form-group">
      <button
        type="button"
        class="submit-button"
        @click="changePassword"
        :disabled="!isEmailSent"
      >
         
      </button>
    </div>

    <hr />

    <h2> </h2>
    <button class="delete-account-button" @click="confirmDeactivateAccount">
       
    </button>
  </div>
</template>

<script>
import { useAuthStore } from "@/store/register_login";

export default {
  name: "UserEditPage",
  setup() {
    const authStore = useAuthStore();
    const userInfo = JSON.parse(localStorage.getItem("userInfo")) || {};

    return {
      userName: userInfo.userName || "",
      email: userInfo.email || "",
      phone: userInfo.phone || "",
      authStore,
    };
  },
  data() {
    return {
      currentPassword: "",
      isPasswordValid: false,
      newPassword: "",
      passwordConfirmation: "",
      isEmailSent: false,
      verificationCode: "",
    };
  },
  methods: {
    async checkPassword() {
      try {
        const response = await this.authStore.verifyPassword(this.currentPassword);
        this.isPasswordValid = response.status === 200;
      } catch (error) {
        this.isPasswordValid = false;
        console.error("  :", error);
      }
    },
    async submitForm() {
      if (this.isPasswordValid) {
        try {
          await this.authStore.updateUserInfo({
            userName: this.userName,
            phone: this.phone,
          });
          alert("   .");
        } catch (error) {
          console.error("   :", error);
          alert("     .   .");
        }
      }
    },
    async sendVerificationCode() {
      if (this.newPassword === this.passwordConfirmation) {
        try {
          await this.authStore.sendVerificationEmail(this.email, "editPassword");
          this.isEmailSent = true;
          alert("  .");
        } catch (error) {
          console.error("   :", error);
          alert("     .   .");
        }
      } else {
        alert("     .");
      }
    },
    async changePassword() {
      try {
        if (this.isEmailSent) {
          await this.authStore.changePassword(this.newPassword);
          alert("  .");
        } else {
          alert(" .");
        }
      } catch (error) {
        console.error("  :", error);
        alert("    .   .");
      }
    },
    confirmDeactivateAccount() {
      const isConfirmed = confirm("  ?     .");
      if (isConfirmed) {
        this.deactivateAccount();
      }
    },
    async deactivateAccount() {
      try {
        await this.authStore.deactivateUser();
        alert("  .");
        this.authStore.logout();
        window.location.href = "/";
      } catch (error) {
        console.error("  :", error);
        alert("    .   .");
      }
    },
  },
};
</script>
  
<style scoped>
.user-edit-container {
  max-width: 600px;
  margin: 0 auto;
  background: #ffffff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

.form-group input {
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.info-text {
  color: #888;
  font-size: 0.9em;
}

.error-text {
  color: red;
  font-size: 0.9em;
}

.submit-button {
  background: #007bff;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.submit-button:disabled {
  background: #cccccc;
}

.submit-button:hover {
  background: #0056b3;
}

.send-email-button {
  margin-top: 10px;
  padding: 10px;
  background: #28a745;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.send-email-button:hover {
  background: #218838;
}

.delete-account-button {
  margin-top: 20px;
  padding: 10px;
  background: #dc3545;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.delete-account-button:hover {
  background: #c82333;
}
</style>
