import { defineStore } from 'pinia';
import { signupUser, loginUser, checkEmail, sendVerificationEmailAPI, verifyEmailToken, updateUserInfoAPI, verifyPasswordAPI, changePasswordAPI, deactivateUserAPI, getUserInfo } from '@/api/api';

/** /,   */
export const useAuthStore = defineStore('auth', {
  state: () => ({
    currentUser: null,
    accessToken: null,
    LoggedIn: false,
    emailMessage: "", //    
    isEmailAvailable: false, //    
    isVerified: false, //   
    verificationToken: null, //    
    userId: null,
    userName: null,
    phone: null,
  }),

  actions: {
    /**   */
    async signup(userData) {
      try {
        if (!this.verificationToken) {
          throw new Error("   .");
        }

        const response = await signupUser(userData, this.verificationToken);
        return response;
      } catch (error) {
        console.error(error);
        throw error;
      }
    },

    /**    */
    async sendVerificationEmail(email, mode) {
      try {
        const response = await sendVerificationEmailAPI(email, mode);
        this.isVerified = true;
        this.verificationToken = response.data; //   JWT   

        //    
        localStorage.setItem('verificationToken', response.data);
        return response;
      } catch (error) {
        console.error("    :", error);
        this.isVerified = false;
        throw error;
      }
    },

    /**       */
    async verifyEmailToken(token) {
      try {
        const response = await verifyEmailToken(token);
        if (response && response.status === 200) {
          this.isVerified = true;
          this.verificationToken = token; //  JWT   
        }
        return response;
      } catch (error) {
        console.error("  :", error);
        this.isVerified = false;
        throw error;
      }
    },

    /**    */
    async checkEmailAvailability(email) {
      try {
        const response = await checkEmail(email);
        this.emailMessage = response.data;
        this.isEmailAvailable = true;
      } catch (error) {
        if (error.response && error.response.status === 409) {
          this.emailMessage = error.response.data;
        } else {
          this.emailMessage = "     .";
        }
        this.isEmailAvailable = false;
      }
    },

    /**   */
    async login(userData) {
      try {
        const response = await loginUser(userData);

        //      
        this.currentUser = response.data.email;  //   
        this.LoggedIn = true;
        this.setAccessToken(response.data.token);
        this.phone = response.data.phone;
        this.userName = response.data.userName;
        this.userId = response.data.userId;

        console.log(' :', response.data);

        //   LocalStorage  (  )
        localStorage.setItem('userInfo', JSON.stringify({
          userName: response.data.userName,
          userId: response.data.userId,
          email: response.data.email,
          phone: response.data.phone,
        }));

        //   
        sessionStorage.setItem('token', response.data.token);

      } catch (error) {
        console.error(' :', error);
        throw new Error(' ');
      }
    },

    /**    */
    setAccessToken(token) {
      this.accessToken = token;
      this.LoggedIn = !!token;
      sessionStorage.setItem('token', token);
    },

    /**  */
    logout() {
      this.currentUser = null;
      this.accessToken = null;
      this.LoggedIn = false;
      this.userId = null;
      this.userName = null;
      this.phone = null;

      //     
      sessionStorage.removeItem('token');
      localStorage.removeItem('userInfo');
      localStorage.removeItem('verificationToken');
      localStorage.removeItem('verifiedEmail');
      console.log(' ');
    },

    /**    */
    checkLoginStatus() {
      const token = sessionStorage.getItem('token');
      const userInfo = localStorage.getItem('userInfo');

      if (token) {
        this.accessToken = token;
        this.LoggedIn = true;

        if (userInfo) {
          const parsedUserInfo = JSON.parse(userInfo);
          this.userName = parsedUserInfo.userName;
          this.userId = parsedUserInfo.userId;
          this.currentUser = parsedUserInfo.email;
          this.phone = parsedUserInfo.phone;
        }
      } else {
        this.LoggedIn = false;
      }
    },

    /**    */
    async updateUserInfo(userInfo) {
      try {
        const response = await updateUserInfoAPI(userInfo, this.accessToken);
        if (response.status === 200) {
          //   LocalStorage 
          localStorage.setItem('userInfo', JSON.stringify({
            ...JSON.parse(localStorage.getItem('userInfo')),
            ...userInfo
          }));
          // Pinia  
          this.userName = userInfo.userName;
          this.phone = userInfo.phone;
          console.log('   :', response.data);
        }
        return response;
      } catch (error) {
        console.error('   :', error);
        throw error;
      }
    },

    /**   */
    async verifyPassword(password) {
      try {
        const response = await verifyPasswordAPI(password, this.accessToken);
        return response;
      } catch (error) {
        console.error('  :', error);
        throw error;
      }
    },

    /**   */
    async changePassword(newPassword) {
      try {
        const email = this.currentUser;
        const token = localStorage.getItem("verificationToken");
        const response = await changePasswordAPI(email, newPassword, token);
        return response;
      } catch (error) {
        console.error('  :', error);
        throw error;
      }
    },

    /**   */
    async deactivateUser() {
      try {
        const response = await deactivateUserAPI(this.accessToken);
        return response;
      } catch (error) {
        console.error('  :', error);
        throw error;
      }
    },

    /**    */
    async getUserInfo() {
      try {
        const token = this.accessToken;
        if (!token) throw new Error(" .");

        const response = await getUserInfo(token);
        return response;
      } catch (error) {
        console.error("   :", error);
        throw error;
      }
    }
  }
});