<template>
  <div class="mypage-container">
    <!--   -->
    <SidebarComponent class="sidebar" />

    <!--   -->
    <div class="router-view-container">
      <router-view v-slot="{ Component }">
        <component
          :is="Component"
          :user-name="userName"
          :email="email"
          :phone="phone"
          :reviews="reviews"
          @update="handleReviewUpdated"
          :logged-in-user-id="loggedInUserId"
        />
      </router-view>
    </div>

    <!--     -->
    <div class="favorite-hotels">
      <h2>  </h2>
      <div class="hotel-list">
        <div class="hotel-item" v-for="(hotel, index) in favoriteHotels" :key="index" >
          <!-- router-link     -->
          <router-link :to="`/hotel-details/${hotel.hotelId}`" class="hotel-link" >
            <img :src="hotel.hotelImage" alt="Hotel Image" class="hotel-image" />
            <span class="hotel-name">{{ hotel.hotelName }}</span>
          </router-link>
        </div>
        <div v-if="favoriteHotels.length === 0">   .</div>
      </div>
    </div>
  </div>
</template>

<script>
import SidebarComponent from "@/components/UserPages/UserMyPageSidebar.vue";
import { getReviewsByUser, getFavoriteInfo } from "@/api/api";

export default {
  name: "MyPage",
  components: {
    SidebarComponent,
  },
  data() {
    return {
      userName: "",
      email: "",
      phone: "",
      reviews: [],
      favoriteHotels: [],
      loggedInUserId: null, //    ID
      intervalId: null,
    };
  },
  mounted() {
    const userInfo = JSON.parse(localStorage.getItem("userInfo"));
    if (userInfo) {
      this.userName = userInfo.userName || " .";
      this.email = userInfo.email || " .";
      this.phone = userInfo.phone || " .";
      this.loggedInUserId = userInfo.userId; //    ID 

      this.fetchUserReviews();
      this.fetchUserFavorite();
      this.startFetchingReviews();
    } else {
      console.log("  userInfo .");
    }
  },
  beforeUnmount() {
    if (this.intervalId) {
      clearInterval(this.intervalId);
    }
  },
  methods: {
    async fetchUserReviews() {
      try {
        const token = sessionStorage.getItem("token");
        const response = await getReviewsByUser(this.loggedInUserId, token);
        this.reviews = response.data;
      } catch (error) {
        console.error("    :", error);
      }
    },
    startFetchingReviews() {
      this.intervalId = setInterval(this.fetchUserReviews, 30000);
    },
    async fetchUserFavorite() {
      try {
        const token = sessionStorage.getItem("token");
        const response = await getFavoriteInfo(token);
        this.favoriteHotels = response.data;
      } catch (error) {
        console.error(
          "      :",
          error
        );
      }
    },
    handleReviewUpdated() {
    this.fetchUserReviews();
  },
  },
};
</script>

<style scoped>
.mypage-container {
  display: flex;
  justify-content: space-between; /* , ,       */
  align-items: flex-start; /*     */
  margin: 20px;
}

.sidebar {
  width: 200px;
  flex-shrink: 0; /*        */
  background: #f0f0f0;
  padding: 20px;
  border-radius: 10px;
  margin-right: 20px; /*       */
}

.router-view-container {
  flex-grow: 1; /*      */
  padding: 20px;
  background: #ffffff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  min-height: 500px; /*          */
  margin-right: 20px; /*          */
}

.favorite-hotels {
  width: 250px;
  flex-shrink: 0; /*       */
  padding: 20px;
  background: #f7f7f7;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  align-self: flex-start; /*          */
}

.hotel-item a{
  display: flex;
  align-items: center; /*      */
  gap: 15px; /*     */
  padding: 10px;
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  text-decoration-line: none;
}

.hotel-image {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 5px;
}

.hotel-name {
  font-size: 14px;
  color: #333333;
}
</style>
